<template>
    <div class="slide_page">
        <div>
            <mt-cell class="cell_item" title="开启定时开关" label="关闭后设定时间无效">
                <mt-switch v-model="timing_on"></mt-switch>
            </mt-cell>
        </div>
        <div v-if="timing_on" class="cell_list select_time">
            <mt-cell @click.native="openPicker('onTime')" class="cell_item" title="开机时间">
                <span class="time">{{on_time}}</span>
            </mt-cell>
            <mt-cell @click.native="openPicker('offTime')" class="cell_item" title="关机时间">
                <span class="time">{{off_time}}</span>
            </mt-cell>
        </div>
        <div style="margin: .8rem .24rem 0;">
            <mt-button @click="save_seting" type="primary" size="large">保存</mt-button>
        </div>

        <mt-datetime-picker ref="onTime" v-model="on_time" type="time" :modal="true" @confirm="confirm_onTime"></mt-datetime-picker>
        <mt-datetime-picker ref="offTime" v-model="off_time" type="time" :modal="true" @confirm="confirm_offTime"></mt-datetime-picker>
    </div>
</template>

<script>
    export default {
        name: "TimingSwitch",
        data() {
            return {
                timing_on: true,
                on_time: '06:00',
                off_time: '18:00',
            }
        },
        created(){
            if( this.$mqtt ){
                // 发布 空 , 为了获取 物影子上次数据
                this.$mqtt.Client.publish('$baidu/iot/shadow/gh_0ddfb722a565_c9edd422aafdc95f/get', JSON.stringify({ 'residired': {}, 'reported': {}}));
            }
            window.eventBus.$on('baidu_shadow', res => {
                console.log(res);
                var reported = res.message.reported;
                // 定时开关机 
                if ('timing' in reported ) {
                    let timing = reported.timing;
                    this.timing_on = timing.timing_on == 1 ? true : false;
                    this.on_time = timing.timing_start_hour +':'+ timing.timing_start_min;
                    this.off_time = timing.timing_end_hour +':'+ timing.timing_end_min;
                }
            });
        },
        methods: {
            // 打开 picker
            openPicker(picker){
                this.$refs[picker].open();
            },
            // 开机时间
            confirm_onTime(time){
                console.log(time);
                this.on_time = time.toString();
            },
            // 关机 时间
            confirm_offTime(time){
                console.log(time);
                this.off_time = time.toString();
            },
            // 保存
            save_seting(){
                console.log(this.timing_on);
                let timing_on = this.timing_on ? 1 : 0;
                let timing = {
                    'timing_on': timing_on
                };
                // 定时开机
                let timing_start = this.on_time.split(':');
                let timing_start_hour = timing_start[0];
                let timing_start_min = timing_start[1];
                // console.log(this.on_time);
                // console.log(timing_start);
                // console.log(timing_start_hour);
                // console.log(timing_start_min);
                // 定时关机
                let timing_end = this.off_time.split(':');
                let timing_end_hour = timing_end[0];
                let timing_end_min = timing_end[1];
                // console.log(this.off_time);
                // console.log(timing_end);
                // console.log(timing_end_hour);
                // console.log(timing_end_min);

                if(timing_on == 1){
                    timing.timing_start_hour = timing_start_hour;
                    timing.timing_start_min = timing_start_min;
                    timing.timing_end_hour = timing_end_hour;
                    timing.timing_end_min = timing_end_min;
                }


                // this.$mqtt.publish({'desired': {timing: timing}});
                this.$mqtt.publish({'desired': {timing: timing}});
            },
        },
        destroyed(){
            window.eventBus.$off('baidu_shadow');
        }
    };
</script>

<style scoped>
    .slide_page {
        padding: .24rem 0;
    }
    .select_time {
        margin: .8rem 0 0;
    }
    /* .select_time>.cell_item:after {
        box-sizing: border-box;
        margin: 0 .24rem;
    } */
    /* .select_time .cell_item>.mint-cell-wrapper {
        padding: 0 .35rem;
    } */
    .select_time .time {
        color: #26a2ff;
    }
</style>